'use client';

import { useQuery, QueryClient, hydrate } from '@tanstack/react-query';
import type { DehydratedState } from '@tanstack/react-query';
import { fetchNoteById } from '../../lib/api';
import type { Note } from '../../types/note';

interface Props {
  dehydratedState: DehydratedState;
  noteId: string;
}

const NoteDetailsClient = ({ dehydratedState, noteId }: Props) => {
  const queryClient = new QueryClient();


  hydrate(queryClient, dehydratedState);

  const { data: note, isLoading, error } = useQuery<Note>({
    queryKey: ['note', noteId],
    queryFn: () => fetchNoteById(noteId),
    initialData: () => queryClient.getQueryData(['note', noteId]), 
  });

  if (isLoading) return <p>Loading...</p>;
  if (error || !note) return <p>Error loading note: {(error as Error).message}</p>;

  return (
    <div>
      <h1>{note.title}</h1>
      <p>{note.content}</p>
      {note.tag && <span>Tag: {note.tag}</span>}
    </div>
  );
};

export default NoteDetailsClient;